import React, { useState } from "react";

import ReactFullpage from "@fullpage/react-fullpage";

import Responsive from "react-responsive";

import Hero from "./Hero/";
import Features from "./Features/";
import Special from "./Special";
import Service from "../Service";
import Cards from "../Cards";
import Contacts from "./Contacts";
import Nav from "../Nav";
import { FixedHomeBg, DefaultBg } from "../styled/defaultBg";
import Footer from "../Footer";

const Desktop = props => <Responsive {...props} minWidth={1161} />;
const Tablet = props => <Responsive {...props} maxWidth={1160} />;

const FixedBg = () => <FixedHomeBg id="fixedBg" />;

const HomePage = props => {
  const [isSpecial, setIsSpecial] = useState(false);
  // const [currAnchor, setCurrAnchor] = useState(0);

  let isFirstRender = true;

  const anchors = [
    "hero",
    "features",
    "special",
    "service",
    "cards",
    "contacts"
  ];
  const anchorsName = {
    features: "",
    special: "  ",
    services: " ",
    cards: " ",
    contacts: ""
  };

  // const rotateDeg = {
  //   hero: 0,
  //   features: 50,
  //   special: 100,
  //   service: 150,
  //   cards: 200,
  //   contacts: 250
  // };
  const rotateDeg = [0, 50, 100, 150, 200, 250];

  let setAllowScrolling = null;

  // let currentAnchor = null;

  // const afterSlideLoad = () => {
  //   if (typeof setAllowScrolling === "function") {
  //     console.log("tttt");
  //     if (
  //       // elmAnchor === anchors[1] ||
  //       // elmAnchor === anchors[2] ||
  //       props.scrolDisable
  //     ) {
  //       console.log("disabled scroll");
  //       setAllowScrolling(false);
  //     } else {
  //       setAllowScrolling(true);
  //     }
  //   }
  // };

  const afterLoad = (origin, destination, direction) => {
    const elmAnchor = destination.anchor;
    if (typeof setAllowScrolling === "function") {
      if (elmAnchor === anchors[1] || elmAnchor === anchors[2]) {
        // console.log("disabled scroll");
        setAllowScrolling(false);
      } else {
        setAllowScrolling(true);
      }
    }
  };

  const onLeave = (origin, destination, direction) => {
    // currentAnchor = destination.anchor;
    // console.log(destination)
    // setCurrAnchor(destination.index);
    props.logoRotateSet(rotateDeg[destination.index]);

    if (destination.anchor === anchors[2]) {
      setIsSpecial(true);
    }
  };

  return (
    <div>
      <Desktop>
        <FixedBg />
        {/* <Nav logoRotateDeg={rotateDeg[currAnchor]} /> */}

        <ReactFullpage
          licenseKey="OPEN-SOURCE-GPLV3-LICENSE"
          anchors={anchors}
          lockAnchors={true}
          slidesNavigation={true}
          // fixedElements={["#header", "#fixedBg"]}
          responsiveWidth="1161"
          afterLoad={(...args) => afterLoad(...args)}
          onLeave={(...args) => onLeave(...args)}
          render={({ state, fullpageApi }) => {
            if (state.initialized === true) {
              setAllowScrolling = fullpageApi.setAllowScrolling;

              if (isFirstRender) {
                if (props.scrollDisable) {
                  setAllowScrolling(false);
                } else {
                  setAllowScrolling(true);
                  isFirstRender = false;
                }
              }
            }

            return (
              <ReactFullpage.Wrapper>
                {/* <FixedBg />

                <Nav logoRotateDeg={rotateDeg[currentAnchor]} /> */}

                <Hero
                  fullpage={fullpageApi}
                  nextBlockName={anchorsName.features}
                />
                <Features
                  fullpage={fullpageApi}
                  nextBlockName={anchorsName.special}
                />
                <Special
                  fullpage={fullpageApi}
                  nextBlockName={anchorsName.services}
                  isCurrent={isSpecial}
                />
                <Service
                  isHome={true}
                  fullpage={fullpageApi}
                  nextBlockName={anchorsName.cards}
                />
                <Cards
                  isHome={true}
                  fullpage={fullpageApi}
                  nextBlockName={anchorsName.contacts}
                />
                <Contacts isHome={true} />
              </ReactFullpage.Wrapper>
            );
          }}
        />
      </Desktop>

      <Tablet>
        <DefaultBg>
          <Nav />

          <Hero />
          <Features nextBlockName={anchorsName.special} />
          <Special nextBlockName={anchorsName.services} isCurrent={isSpecial} />
          <Service isHome={true} nextBlockName={anchorsName.cards} />
          <Cards isHome={true} nextBlockName={anchorsName.contacts} />
          <Contacts isHome={true} mobile={true} />
          <Footer />
        </DefaultBg>
      </Tablet>
    </div>
  );
};

export default HomePage;
