import React from "react";
import styled from "styled-components";

import vk from "../../img/icon-soc-vk.svg";
import vkWhite from "../../img/icon-soc-vk-white.svg";
import inst from "../../img/icon-soc-inst.svg";
import instWhite from "../../img/icon-soc-inst-white.svg";
import fb from "../../img/icon-soc-fb.svg";
import fbWhite from "../../img/icon-soc-fb-white.svg";

const SIZE_SOC_ICON = 36;

const Wrapper = styled.div`
  display: flex;
  flex-direction: ${props => (props.row ? "row" : "column")};
  justify-content: space-between;
  align-items: center;

  width: ${props => (props.row ? `${props.count * SIZE_SOC_ICON}px` : "24px")};
  height: ${props => (props.row ? "24px" : `${props.count * SIZE_SOC_ICON}px`)};
`;

const SocLink = styled.a.attrs(href => ({
  href: href || ""
}))`
  display: flex;
  justify-content: center;
  align-items: center;

  width: 24px;
  height: 24px;
  background-image: url(${props => {
    if (props.color === "white") {
      switch (props.name) {
        case "vk":
          return vkWhite;
        case "inst":
          return instWhite;
        case "fb":
          return fbWhite;
        default:
          break;
      }
    } else {
      switch (props.name) {
        case "vk":
          return vk;
        case "inst":
          return inst;
        case "fb":
          return fb;
        default:
          break;
      }
    }
  }});

  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
`;

const Social = props => (
  <Wrapper row={props.row} count={props.socialData.length}>
    {props.socialData.map(elm => (
      <SocLink key={elm.name} color={props.color} {...elm} />
    ))}
  </Wrapper>
);

export default Social;