const mobile = isMobile();

const MenuList = props => {
  // const createRefs = (obj, ref)
  const [active, setActive] = useState(null);
  const [activeSub, setActiveSub] = useState(null);

  const [mainActive, setMainActive] = useState(true);
  const [subActive, setSubActive] = useState(false);
  const [subSubActive, setSubSubActive] = useState(false);

  useEffect(() => {
    setActiveSub(null);
  }, [active]);

  let menuRef = {};
  const obj = {
    main: {
      ref: "dom",
      children: {
        ref: "dom"
      }
    }
  };

  return (
    <div className="flex">
      <div className={mainActive ? "first" : "first left"}>
        <FirstList>
          {data.map(main => (
            //first
            <ListItem
              key={main.id}
              onClick={() => {
                if (menuRef[main.name]) {
                  setActive(main.name);

                  if (mobile) {
                    setMainActive(false);
                    setSubActive(true);
                  }
                } else {
                  setActive(null);
                }
              }}
              active={active === main.name}
              hasChildren={main.children}
            >
              {main.name}
            </ListItem>
          ))}
        </FirstList>
      </div>

      <div className={subActive ? "second left" : "second" }>
        {data.map(main => {
          if (main.children) {
            return (
              <ListWrapper
                key={main.id}
                ref={e => {
                  menuRef = {
                    ...menuRef,
                    [main.name]: {
                      ref: e
                    }
                  };
                }}
              >
                <DecoreLine
                  pose={active === main.name ? "visible" : "hidden"}
                />
                <SecondList
                  pose={active === main.name ? "visible" : "hidden"}
                  visible={active === main.name}
                >
                  {main.children.map(elm => (
                    <ListItem
                      key={elm.id}
                      onClick={() => {
                        if (menuRef[main.name]) {
                          setActiveSub(elm.name);
                        } else {
                          setActiveSub(null);
                        }
                      }}
                      hasChildren={elm.children}
                      active={activeSub === elm.name}
                    >
                      {elm.name}
                    </ListItem>
                  ))}
                </SecondList>
              </ListWrapper>
            );
          }
        })}
      </div>

      <div className="second">
        {data.map(main => {
          if (main.children) {
            return main.children.map(sub => {
              if (sub.children) {
                return (
                  <ListWrapper
                    key={sub.id}
                    ref={e => {
                      menuRef = {
                        ...menuRef,
                        [main.name]: {
                          ...menuRef[main.name],
                          childrenRef: e
                        }
                      };
                    }}
                  >
                    <DecoreLine
                      pose={activeSub === sub.name ? "visible" : "hidden"}
                    />
                    <SecondList
                      pose={activeSub === sub.name ? "visible" : "hidden"}
                      visible={activeSub === sub.name}
                    >
                      {sub.children.map(elm => (
                        <ListItem key={elm.id} noOpacity={true}>
                          {elm.name}
                        </ListItem>
                      ))}
                    </SecondList>
                  </ListWrapper>
                );
              }
            });
          }
        })}
      </div>
    </div>
  );
};

export default MenuList;
