import React, { useState, useEffect } from "react";
import { Route } from "react-router-dom";
import styled, { css, createGlobalStyle } from "styled-components";
import reset from "styled-reset";
import posed, { PoseGroup } from "react-pose";

import fontRaleway from "./fonts/raleway.css";
import greyBg from "./img/grey_bg.png";

import HomePage from "./components/Home";
import Gallery from "./components/Gallery";
import Preloader from "./components/Preloader";
import Nav from "./components/Nav";
import Menu from "./components/Menu";

const GlobalFont = css`
  ${fontRaleway}

  body {
    @import url("https://fonts.googleapis.com/css?family=Roboto:100,300,400,500,700&subset=cyrillic");
    font-family: "Roboto", sans-serif !important;
    color: #444952;
  }
`;

const GlobalButton = css`
  button {
    font-family: inherit;
    cursor: pointer;
  }
`;

const GlobalStyles = createGlobalStyle`
  ${reset};

  ${GlobalFont}
  ${GlobalButton}

  * {
    box-sizing: border-box;
  }

  #root {
    position: relative;
    z-index: 10;
    overflow: hidden;
  }

  #fullpage {
    z-index: 10;
  }
`;

