const PoseWrapper = styled(
  posed.div({
    enter: { opacity: 1 },
    exit: { opacity: 0, transition: { duration: 600 } }
  })
)`
  position: fixed;
  z-index: 1000;
  opacity: 0.5;
`;

function preventDefault(e) {
  console.log("disabled scroll");
  e = e || window.event;
  e.stopPropagation();
  if (e.preventDefault) e.preventDefault();
  e.returnValue = false;
}

const App = () => {
  const [loading, setLoading] = useState(false);
  const [loadTime, setLoadTime] = useState(false);

  // nav and menu
  const [logoRotate, setLogoRotate] = useState(0);
  const [menuOpen, setMenuOpen] = useState(true);

  // let loadTime = false;

  useEffect(() => {
    if (loading) {
      // window.addEventListener('DOMMouseScroll', preventDefault, false);
      // window.onwheel = preventDefault;
      // window.onmousewheel = document.onmousewheel = preventDefault;

      window.onload = e => {
        const time = e.timeStamp / 1000;
        setLoadTime(time);
        setTimeout(() => setLoading(false), time * 1000);
      };
    }
  });

  return (
    <React.Fragment>
      <GlobalStyles />

      <PoseGroup>
        {loading && (
          <PoseWrapper key="preloader">
            <Preloader time={loadTime} />
          </PoseWrapper>
        )}
      </PoseGroup>

      <Nav
        logoRotateDeg={logoRotate}
        menuOpen={menuOpen}
        setMenuOpen={setMenuOpen}
      />
      <Menu menuOpen={menuOpen} />

      {/* {!loading && <HomePage />} */}
      <HomePage scrollDisable={loading} logoRotateSet={setLogoRotate} />
      {/* <Route exact path="/" component={HomePage} />
      <Route path="/gallery" component={Gallery} /> */}
    </React.Fragment>
  );
};

export default App;
